.cards-container {
  width: 100%;
  margin-top: 1rem;
  margin-bottom: 3rem;
}

.no-matches {
  color: var(--text);
  text-align: center;
  font-size: 1.2rem;
}

@media screen and (min-width: 768px) {
  .cards-container {
    display: flex;
    gap: 2rem;
    flex-wrap: wrap;
    justify-content: center;
    margin-top: 2rem;
  }
}
